const cityData = require('@/static/city.js')
import { mapState, mapActions, mapMutations } from 'vuex';
export default {
	data() {
		return {
			lists: cityData.list,
			touchmove: false, // 是否在索引表上滑动
			touchmoveIndex: -1,
			titleHeight: 132, // 索引二字距离窗口顶部的高度
			indexBarHeight: 0, // 索引表高度
			indexBarItemHeight: 0, // 索引表子项的高度
			scrollViewId: '', // scroll-view滚动到的子元素的id
			winHeight: 0,
			inputVal: '', // 搜索框输入的内容
			hotCity: ['北京', '上海', '广州', '深圳', '杭州', '长沙', '武汉', '厦门', '西安', '昆明', '成都', '重庆'], // 热门城市
			localCity: ''
		}
	},
	computed:{
		...mapState({
			curruntCity: state => state.curruntCity,
			isEnd:state => state.isEnd,
		})
	},
	mounted () {
		const that = this;
		setTimeout(() => {
			uni.getSystemInfo({
				success: function(res) {
					console.log(res);
					let winHeight = res.windowHeight - uni.upx2px(100)
					let barHeight = winHeight - uni.upx2px(204);
					that.winHeight = winHeight;
					that.indexBarHeight = barHeight;
					that.indexBarItemHeight = barHeight / 25;
					that.titleHeight = uni.upx2px(132);
					that.lists = cityData.list
				}
			})
		}, 50)
	},
	methods: {
		...mapActions(['startLocaltion']),
		handleLocaltion(){
			this.startLocaltion()
		},
		showInput() {
			this.inputShowed = true
		},
		clearInput() {
			this.inputVal = "";
			this.inputShowed = false;
			this.searchResult = [];
			uni.hideKeyboard() //强行隐藏键盘
		},
		// 选择城市
		selectCity(e) {
			let cityName = e.currentTarget.dataset.name;
			//返回并刷新上一页面
			this.$emit('selectCity', cityName)
		},
		touchStart(e) {
			this.touchmove = true
			let pageY = e.touches[0].pageY
			let index = Math.floor((pageY - this.titleHeight) / this.indexBarItemHeight)
			let item = this.lists[index === 0 ? 1 : index]
			if (item) {
				this.scrollViewId = item.letter;
				this.touchmoveIndex = index;
			}
		},
		touchMove(e) {
			let pageY = e.touches[0].pageY;
			let index = Math.floor((pageY - this.titleHeight) / this.indexBarItemHeight)
			let item = this.lists[index === 0 ? 1 : index]
			if (item) {
				this.scrollViewId = item.letter;
				this.touchmoveIndex = index
			}
		},
		touchEnd() {
			this.touchmove = false;
			this.touchmoveIndex = -1;
		},
		touchCancel() {
			this.touchmove = false;
			this.touchmoveIndex = -1;
		}
	}
}
